body {
  background-color: #7fffd4;
}

.query-to-axios {
  width: 400px;
  height: 30px;
  background-color: #fff;
  border-style: solid;
  outline: 5px;
  margin-top: 50px;
}

.container {
  justify-content: center;
  align-items: center;
  display: flex;
}

input[type="text"] {
  border-color: #d60a3a;
  border-radius: 23px;
}

.submit {
  width: 80px;
  height: 30px;
  margin-top: 50px;
  margin-left: 20px;
}

button[type="submit"] {
  color: pink;
  background-color: #00f;
  border-radius: 20px;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, .4);
}

.newsWrapper {
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.article-img {
  width: 400px;
  display: inline-block;
}

.article-description, .article-title {
  width: 400px;
}

.article-card {
  width: 400px;
  background-color: #dee8f5;
  border-radius: 20px;
  padding: 10px;
}

.is-hidden {
  pointer-events: none;
  visibility: 1;
  display: none;
}

.gallery {
  margin-top: 10px;
}

.photo-card {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.photo-card img {
  max-width: 100%;
  border-radius: 10px;
  margin-bottom: 10px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .3);
}

